












































































































































.miller {
  display: flex;
  min-height: 600px;
  margin: 2rem 0;
  color: black;
  background: #fafafa;

  &__col {
    display: flex;
    flex-direction: column;
    width: auto;
    min-width: 160px;
    margin-right: -1px;
    border: 1px solid #dddddd;

    &__header {
      padding: 1rem 1rem;
      font-weight: 500;
      line-height: 1rem;
      background: #eeeeee;
      border-bottom: 1px solid #dddddd;
    }

    &__item {
      display: block;
      width: 100%;
      padding: 0.5rem 3rem 0.5rem 1rem;
      user-select: none;

      &:hover:not(.active) {
        background: rgba(black, 0.03);
        cursor: pointer;
      }

      &.active {
        background: #dddddd;
      }
    }

    &__add {
      // margin-top: auto;
      margin-bottom: -1px;
      color: #666666;
      border-top: 1px solid #dddddd;
      border-bottom: 1px solid #dddddd;
    }
  }

  &__main {
    flex-grow: 1;
    padding: 2rem;
    background: white;
    border: 1px solid #dddddd;
  }

  &--list {
    padding: 0;
    list-style-type: none;
  }
}
